<template>
  <div class="container">
    <img src="../../public/fnsvalue_LOGO.svg" alt="BSA 로고" class="logo">
    <div class="language-toggle" @click="toggleLanguage">{{ currentLanguage }}</div>
    <h1 class="page-title">{{ pageTitle }}</h1>
    <div class="header">
      <h2 class="header-text">{{ deletionRequest }}</h2>
    </div>
    <div class="content">
      <p>{{ step1 }}</p>
      <ol>
        <li>{{ step1Info }}</li>
        <li>{{ step2Info }}</li>
      </ol>
      <p>{{ deletionInfo }}</p>
        <ul>
          <li>{{ deletionInfo1 }}</li>
          <li>{{ deletionInfo2 }}</li>
        </ul>
      <p>{{deletionConfirm}}</p>
      <div style="display: flex; justify-content: center;">
        <router-link to="/otp-verification" class="button">{{ proceedButton }}</router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      languages: ['한국어', 'English'],
      currentLanguageIndex: 1, // 한국어가 기본 설정
      texts: {
        '한국어': {
          pageTitle: 'BSA',
          deletionRequest: 'BSA 계정 삭제 요청',
          step1: '계정 삭제 요청을 위해 다음의 단계를 따라야합니다:',
          step1Info: '사용자의 가입 정보 입력 (아이디, 이메일 또는 휴대전화번호)',
          step2Info: 'OTP 검증',
          deletionInfo: '삭제되는 정보는 아래와 같습니다.',
          deletionInfo1:"사용자 정보",
          deletionInfo2:"사용자의 기기 정보",
          deletionConfirm:"삭제 요청 이후 사용자 정보가 영구적으로 삭제되므로 복구가 불가능합니다.",
          proceedButton: '동의하고 진행'
        },
        'English': {
          pageTitle: 'BSA',
          deletionRequest: 'BSA Account Delete Request',
          step1: 'To request account deletion, follow these steps:',
          step1Info: 'Enter user registration information (ID and email, or phone number)',
          step2Info: 'OTP verification',
          deletionInfo: 'The following information will be deleted. ',
          deletionInfo1:"User information",
          deletionInfo2:"User device information",
          deletionConfirm:"After the deletion request, user information will be permanently deleted and cannot be recovered.",
          proceedButton: 'Agree and proceed'
        }
      }
    };
  },
  computed: {
    currentLanguage() {
      return this.languages[this.currentLanguageIndex];
    },
    currentTexts() {
      return this.texts[this.currentLanguage];
    },
    pageTitle() {
      return this.currentTexts.pageTitle;
    },
    deletionRequest() {
      return this.currentTexts.deletionRequest;
    },
    step1() {
      return this.currentTexts.step1;
    },
    step1Info() {
      return this.currentTexts.step1Info;
    },
    step2Info() {
      return this.currentTexts.step2Info;
    },
    deletionInfo() {
      return this.currentTexts.deletionInfo;
    },
    deletionInfo1() {
      return this.currentTexts.deletionInfo1;
    },
    deletionInfo2() {
      return this.currentTexts.deletionInfo2;
    },
    deletionConfirm() {
      return this.currentTexts.deletionConfirm;
    },
    proceedButton() {
      return this.currentTexts.proceedButton;
    }
  },
  methods: {
    toggleLanguage() {
      // 현재 언어 인덱스를 변경하여 다음 언어로 전환
      this.currentLanguageIndex = (this.currentLanguageIndex + 1) % this.languages.length;
      // 여기에서 언어 변경에 따른 다른 작업을 수행할 수 있습니다.
    },
  },
};
</script>

