<template>
  <div class="container">
    <img src="../../public/fnsvalue_LOGO.svg" alt="BSA 로고" class="logo">
    <div class="language-toggle" @click="toggleLanguage">{{ currentLanguage }}</div>
    <h1 class="page-title">{{ pageTitle }}</h1>
    <div class="header">
      <h2 class="header-text">{{ deletionRequest }}</h2>
    </div>
    <div class="content">
      <div class="otp-verification">
        <p>{{ userConfirmation }}</p>
        <div>
          <label for="user-key">{{ userIdLabel }}</label>
          <input type="text" v-model="userKey" id="user-key" :placeholder="userIdPlaceholder">
        </div>
        <div>
          <label for="verification-type">{{ otpMethodLabel }}</label>
          <select v-model="verificationType" id="verification-type">
            <option value="email">{{ emailOption }}</option>
            <option value="sms">{{ smsOption }}</option>
          </select>
        </div>

        <div v-if="verificationType === 'email'">
          <label for="email">{{ emailLabel }}</label>
          <input type="email" v-model="email" id="email" :placeholder="emailPlaceholder">
        </div>
        <div v-else-if="verificationType === 'sms'">
          <label for="phone">{{ phoneLabel }}</label>
          <input type="text" v-model="phoneNumber" id="phone" :placeholder="phonePlaceholder">
          <div>
            <label for="country">{{ countryLabel }}</label>
            <select v-model="selectedCountry" id="country">
              <option :value="''">{{ selectCountry }}</option>
              <option v-for="country in countries" :key="country.code" :value="country.code">{{ country.name }}</option>
            </select>
          </div>
        </div>

        <button @click="verifyUser">{{ verifyUserButton }}</button>
        <button v-if="showVerifyButton" class="sendOTP-button" @click="sendOTP">{{ sendOTPButton }}</button>
        <p v-if="showErrorMessage" class="error-message">{{ errorMessage }}</p>
      </div>
    </div>
    <!-- OTP 코드 입력란 -->
    <div v-if="showOTPInput" class="otp-input">
      <label for="otp-code">{{ otpInputLabel }}</label>
      <input type="text" v-model="authNum" id="otp-code" :placeholder="otpInputPlaceholder">
      <button @click="verifyOTPCode">{{ verifyOTPButton }}</button>
    </div>
    <div style="display: flex; justify-content: center;">
      <button v-if="isNextButtonEnabled" class="button" @click="navigateToNextPage">{{ nextButton }}</button>
    </div>
  </div>
</template>



<script>
import axios from 'axios';

export default {
  data() {
    return {
      languages: ['한국어', 'English'],
      currentLanguageIndex: 1, // 한국어가 기본 설정
      translation: {
        pageTitle: ['BSA', 'BSA'],
        deletionRequest: ['BSA 계정 삭제 요청', 'BSA Account Delete Request'],
        userConfirmation: ['사용자 확인을 위해 다음의 정보를 입력해주세요.', 'Please enter the following information for user confirmation.'],
        userIdLabel: ['사용자 아이디', 'User ID'],
        userIdPlaceholder: ['아이디를 입력해 주세요', 'Please enter your ID'],
        otpMethodLabel: ['OTP 인증 방법 선택', 'Send OTP to'],
        emailOption: ['이메일', 'Email'],
        smsOption: ['SMS', 'SMS'],
        emailLabel: ['Email', 'Email'],
        emailPlaceholder: ['이메일을 입력해 주세요', 'Please enter your email'],
        phoneLabel: ['전화번호', 'Phone number'],
        phonePlaceholder: ['전화번호를 입력해주세요', 'Please enter your phone number'],
        countryLabel: ['국가', 'Country'],
        selectCountry: ['국가를 선택해주세요', 'Please select your country'],
        verifyUserButton: ['사용자 정보 확인', 'Verify user information'],
        sendOTPButton: ['OTP 발송', 'Send OTP'],
        errorMessage: ['오류가 발생했습니다. 다시 시도해주세요.', 'An error occurred. Please try again.'],
        otpInputLabel: ['OTP 코드', 'OTP code'],
        otpInputPlaceholder: ['OTP 코드를 입력해주세요', 'Please enter your OTP code'],
        verifyOTPButton: ['OTP 확인', 'Verify OTP'],
        nextButton: ['다음', 'Next']
      },
      verificationType: 'email', // Default to email verification
      email: '',
      phoneNumber: '',
      userKey: '', // 사용자에게 입력받을 userKey
      verifyType: '', // 이메일이면 CMMDUP001, SMS면 CMMDUP002
      verifyData: '', // 사용자에게 입력받을 이메일 또는 SMS 데이터
      showVerifyButton: false, // '사용자 정보 확인' 버튼 표시 여부
      showErrorMessage: false, // 에러 메시지 표시 여부
      clientKey: "1daec78593a643e6b53ce9803ded5916",
      countries: [
        { code: '82', name: 'Korea (+82)' },
        { code: '60', name: 'Malaysia (+60)' },
        { code: '62', name: 'Indonesia (+62)' },
        { code: '65', name: 'Singapore (+65)' },
        { code: '1', name: 'USA (+1)' },
        { code: '64', name: 'NewZealand (+64)' },
        // 기타 국가 목록 추가
      ],
      selectedCountry: '',
      showOTPInput: false, // OTP 입력란 표시 여부
      authNum: '',
      disposeToken: '', // disposeToken 값 초기화
      isNextButtonEnabled: false,
      seq:'',
    };
  },
  watch: {
    verificationType(newValue) {
      // OTP 인증 방법이 변경될 때마다 OTP 발송 버튼과 OTP 입력창 숨기기
      this.showVerifyButton = false;
      this.showOTPInput = false;
      // verificationType이 email에서 sms로 변경되거나 sms에서 email로 변경될 때마다 감지
      // 변경된 verificationType에 따라 필요한 작업을 수행할 수 있습니다.
      if (newValue === 'email') {
        // email로 변경될 때 수행할 작업
      } else if (newValue === 'sms') {
        // sms로 변경될 때 수행할 작업
      }
    }
  },
  computed: {
    currentLanguage() {
      return this.languages[this.currentLanguageIndex];
    },
    pageTitle() {
      return this.translation.pageTitle[this.currentLanguageIndex];
    },
    deletionRequest() {
      return this.translation.deletionRequest[this.currentLanguageIndex];
    },
    userConfirmation() {
      return this.translation.userConfirmation[this.currentLanguageIndex];
    },
    userIdLabel() {
      return this.translation.userIdLabel[this.currentLanguageIndex];
    },
    userIdPlaceholder() {
      return this.translation.userIdPlaceholder[this.currentLanguageIndex];
    },
    otpMethodLabel() {
      return this.translation.otpMethodLabel[this.currentLanguageIndex];
    },
    emailOption() {
      return this.translation.emailOption[this.currentLanguageIndex];
    },
    smsOption() {
      return this.translation.smsOption[this.currentLanguageIndex];
    },
    emailLabel() {
      return this.translation.emailLabel[this.currentLanguageIndex];
    },
    emailPlaceholder() {
      return this.translation.emailPlaceholder[this.currentLanguageIndex];
    },
    phoneLabel() {
      return this.translation.phoneLabel[this.currentLanguageIndex];
    },
    phonePlaceholder() {
      return this.translation.phonePlaceholder[this.currentLanguageIndex];
    },
    countryLabel() {
      return this.translation.countryLabel[this.currentLanguageIndex];
    },
    selectCountry() {
      return this.translation.selectCountry[this.currentLanguageIndex];
    },
    verifyUserButton() {
      return this.translation.verifyUserButton[this.currentLanguageIndex];
    },
    sendOTPButton() {
      return this.translation.sendOTPButton[this.currentLanguageIndex];
    },
    errorMessage() {
      return this.translation.errorMessage[this.currentLanguageIndex];
    },
    otpInputLabel() {
      return this.translation.otpInputLabel[this.currentLanguageIndex];
    },
    otpInputPlaceholder() {
      return this.translation.otpInputPlaceholder[this.currentLanguageIndex];
    },
    verifyOTPButton() {
      return this.translation.verifyOTPButton[this.currentLanguageIndex];
    },
    nextButton() {
      return this.translation.nextButton[this.currentLanguageIndex];
    }
  },
  methods: {
    toggleLanguage() {
      // 현재 언어 인덱스를 변경하여 다음 언어로 전환
      this.currentLanguageIndex = (this.currentLanguageIndex + 1) % this.languages.length;
      // 여기에서 언어 변경에 따른 다른 작업을 수행할 수 있습니다.
    },
    navigateToNextPage() {
      // router를 사용하여 userKey와 disposeToken 값을 전달
      this.$router.push({
        path: '/account-deletion',
        query: { userKey: this.userKey, disposeToken: this.disposeToken }
      });
    },
    async verifyUser() {
      this.verifyType = this.verificationType === 'email' ? 'CMMDUP001' : 'CMMDUP002';
      // 사용자에게 입력받은 데이터 설정
      if (this.verificationType === 'email') {
        this.verifyData = this.email;
      } else if (this.verificationType === 'sms') {
        // 전화번호의 첫 번째 자리 숫자 제거 후 국가 코드 추가
        const phoneNumberWithoutPrefix = this.phoneNumber.slice(1); // 첫 번째 자리 숫자 제거
        this.verifyData = this.selectedCountry + phoneNumberWithoutPrefix;
      }

      // 사용자에게 입력받은 userKey가 비어 있는지 확인
      if (!this.userKey) {
        this.showErrorMessage = true;
        this.errorMessage = 'Please enter your user key.';
        return;
      }

      const apiUrl = `https://api.onebsa.com.my/api/v3/user/exists?userKey=${this.userKey}&verifyType=${this.verifyType}&verifyData=${this.verifyData}`;

      try {
        const response = await axios.get(apiUrl);

        // API 응답 처리
        console.log('API response:', response.data);
        if (response.data.rtCode === 0) {
          // 사용자 정보가 확인되면 OTP 발송 버튼 표시
          this.showVerifyButton = true;
          this.showErrorMessage = false;
        } else {
          // 사용자 정보가 확인되지 않으면 에러 메시지 표시
          this.showVerifyButton = false;
          this.showErrorMessage = true;
          this.errorMessage = 'Please verify your user information.';
        }
      } catch (error) {
        console.error('There was a problem with the fetch operation:', error.message);
        this.showErrorMessage = true;
        this.errorMessage = 'There was a problem verifying your information. Please try again later.';
      }
    },
    sendOTP() {
      const otpApiUrl = this.verifyType === 'CMMDUP001'
          ? `https://api.onebsa.com.my/api/v3/mail`
          : `https://api.onebsa.com.my/api/v3/sms`;

      const requestData = {
        clientKey: this.clientKey,
        email: this.email,
      };

      if (this.verifyType === 'CMMDUP002') {
        requestData.phoneNum = this.verifyData;
      }

      axios.post(otpApiUrl, requestData)
          .then(response => {
            // OTP 발송 성공 시 추가 작업 수행
            console.log('OTP sent successfully:', response.data);

            // 응답에서 seq 값을 변수에 저장
            this.seq = response.data.data.seq;
            console.log('seq:', this.seq); // seq 값 로그로 출력

            // OTP 입력창과 'OTP 검증' 버튼 활성화
            this.showOTPInput = true;
          })
          .catch(error => {
            console.error('Error sending OTP:', error);
          });
    },
    async verifyOTPCode() {
      // OTP 확인 버튼을 클릭하면 실행되는 메서드

      // verifyType에 따라 API 요청에 필요한 데이터 설정
      const requestData = {
        clientKey: this.clientKey,
        authNum: this.authNum,
        seq: this.seq, // seq 변수 추가
      };

      if (this.verificationType === 'sms') {
        requestData.phoneNum = this.verifyData; // 전화번호 추가
      } else if (this.verificationType === 'email') {
        requestData.email = this.verifyData; // 이메일 추가
      }

      // API 요청 보내기
      const apiUrl = this.verificationType === 'sms'
          ? 'https://api.onebsa.com.my/api/v3/sms/verify'
          : 'https://api.onebsa.com.my/api/v3/mail/verify';

      try {
        const response = await axios.post(apiUrl, requestData);
        // 응답 데이터의 disposeToken 저장
        this.disposeToken = response.data.data.disposeToken;
        // disposeToken을 필요한 곳에 저장하거나 사용할 수 있습니다.
        console.log('disposeToken:', this.disposeToken);

        // 응답의 rtCode가 0인 경우에만 '다음' 버튼을 활성화합니다.
        if (response.data.rtCode === 0) {
          this.isNextButtonEnabled = true;
        } else {
          // rtCode가 0이 아닌 경우에는 오류 처리
          console.error('Error verifying OTP:', response.data.rtMsg);
          // 오류 메시지를 사용자에게 표시하거나 다른 처리를 수행할 수 있습니다.
        }
      }  catch (error) {
        console.error('Error verifying OTP:', error);
        // 오류 처리
      } finally {
        // OTP 확인이 완료되면 다음 버튼 비활성화
      }
    },






    }
};
</script>



<style scoped>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 33%; /* 화면 가로폭의 33%를 차지 */
  margin: 0 auto; /* 가운데 정렬 */
  height: 100vh;
  text-align: center;
}


.logo {
  width: 200px; /* 로고 이미지의 너비 */
  height: auto; /* 높이 자동 조정 */
  margin-bottom: 20px; /* 로고와 헤더 사이 간격 추가 */
  align-self: flex-start; /* 로고를 왼쪽으로 정렬 */
}

.page-title {
  color: #197EC3; /* 페이지 제목 색상 변경 */
  margin: 0;
  font-size: 36px; /* 페이지 제목 폰트 사이즈 변경 */
  margin-bottom: 10px; /* 페이지 제목과 헤더 텍스트 사이 간격 추가 */
}

.header {
  background-color: rgba(25, 126, 195, 0.5);
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; /* 헤더의 너비를 100%로 설정 */
  margin-bottom: 20px; /* 헤더와 내용 사이 간격 추가 */
}

.header-text {
  color: #fff;
  margin: 0; /* 헤더 텍스트의 margin을 0으로 설정 */
  white-space: nowrap; /* 줄 바꿈을 방지하여 한 줄에 텍스트 표시 */
}

.content {
  text-align: left;
}

.button {
  color: #fff;
  background-color: #197EC3;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  text-align: center;
  margin-top: 20px; /* 버튼 위에 여백 추가 */
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .header-text {
    font-size: 14px; /* 작은 화면에서 텍스트 크기를 줄임 */
  }
}

.otp-verification {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;

  border-radius: 5px;
}

label {
  display: block;
  margin-bottom: 5px;
}

input[type="email"],
input[type="text"],
select {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

button {
  background-color: #197EC3;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.sendOTP-button {
  margin-left: 10px; /* 왼쪽 버튼과 간격 추가 */
}

.error-message {
  color: red; /* 붉은색으로 설정 */
}

.otp-input {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 5px;
  background-color: #f9f9f9;
  margin-top: 20px;
}

.language-toggle {
  padding: 5px 10px; /* 토글 버튼의 내부 여백 설정 */
  border: 1px solid #197EC3; /* 토글 버튼 테두리 설정 */
  cursor: pointer;
  border-radius: 20px;
  color: #fff;
  background-color: #197EC3;
  margin-left: auto;

}
</style>
