<template>
  <div class="container">
    <img src="../../public/fnsvalue_LOGO.svg" alt="BSA 로고" class="logo">
    <div class="language-toggle" @click="toggleLanguage">{{ currentLanguage }}</div>
    <h1 class="page-title">{{ pageTitle }}</h1>
    <div class="header">
      <h2 class="header-text">{{ deletionRequest }}</h2>
    </div>
    <div class="content">
      <div style="display: flex; justify-content: center;">
        <div>
          {{ deletionProcess }}
        </div>
      </div>
      <div style="display: flex; justify-content: center;">
        <div>
          <p>{{ informationCheck }}</p>
        </div>
      </div>
      <p>{{ info1 }} <br />{{ info2 }}<br />
        {{ info3 }}</p>
      <div style="display: flex; justify-content: center;">
        <button @click="deleteAccount" class="button">{{ deleteButton }}</button>
      </div>
      <div v-if="deleteSuccess" class="success-message">
        {{ successMessage }}
      </div>
      <div v-if="deleteError" class="error-message">
        {{ errorMessage }}
      </div>
    </div>
  </div>
</template>

<script setup>
import axios from 'axios';
import router from "@/router";
import { useRoute } from "vue-router";
import { ref } from 'vue';

const deleteError = ref(false);
const deleteSuccess = ref(false);
const route = useRoute();
const { userKey, disposeToken } = route.query;

const deleteAccount = async () => {
  try {
    console.log('userKey:', userKey);
    console.log('disposeToken:', disposeToken);
    const response = await axios.delete(`https://api.onebsa.com.my/api/v3/user`, {
      data: {
        userKey: userKey,
        disposeToken: disposeToken
      }
    });
    console.log('Response:', response.data.rtCode);
    let rtCode = response.data.rtCode;
    if (rtCode === 0) {
      deleteSuccess.value = true;
      setTimeout(() => {
        router.push('/').then(() => {
          window.location.reload(); // 페이지 다시 로드
        });
      }, 2000);
    } else {
      deleteError.value = true;
    }
  } catch (error) {
    console.error('Error deleting account:', error);
    deleteError.value = true;
  }
};

</script>

<script>
export default {
  data() {
    return {
      languages: ['한국어', 'English'],
      currentLanguageIndex: 1, // 한국어가 기본 설정
      translation: {
        pageTitle: ['BSA', 'BSA'],
        deletionRequest: ['BSA 계정 삭제 요청', 'BSA Account Delete Request'],
        deletionProcess: ['BSA 계정 삭제를 진행합니다.', 'We are proceeding with the deletion of your BSA account.'],
        informationCheck: ['아래의 안내 사항을 확인해주세요', 'Please check the information below.'],
        info1: ['삭제 요청 시 즉시 사용자의 정보가 영구적으로 삭제되므로 복구하실 수 없습니다.', 'Once the deletion request is submitted, your information will be permanently removed and cannot be recovered.'],
        info2: ['삭제 후 BSA 앱을 다시 이용하시려면 재가입이 필요합니다.', 'If you wish to use the BSA app again after deletion, you will need to re-register.'],
        info3: ['정말로 삭제하시겠습니까?', 'Are you sure you want to delete it?'],
        deleteButton: ['삭제', 'Delete'],
        successMessage: ['삭제가 완료되었습니다.', 'The deletion has been completed.'],
        errorMessage: ['삭제 중 오류가 발생했습니다.', 'An error occurred during the deletion process.'],
      }
    }
  },

  computed: {
    currentLanguage() {
      return this.languages[this.currentLanguageIndex];
    }
    ,
    pageTitle() {
      return this.translation.pageTitle[this.currentLanguageIndex];
    }
    ,
    deletionRequest() {
      return this.translation.deletionRequest[this.currentLanguageIndex];
    }
    ,
    deletionProcess() {
      return this.translation.deletionProcess[this.currentLanguageIndex];
    }
    ,
    informationCheck() {
      return this.translation.informationCheck[this.currentLanguageIndex];
    }
    ,
    info1() {
      return this.translation.info1[this.currentLanguageIndex];
    }
    ,
    info2() {
      return this.translation.info2[this.currentLanguageIndex];
    }
    ,
    info3() {
      return this.translation.info3[this.currentLanguageIndex];
    }
    ,
    deleteButton() {
      return this.translation.deleteButton[this.currentLanguageIndex];
    }
    ,
    successMessage() {
      return this.translation.successMessage[this.currentLanguageIndex];
    }
    ,
    errorMessage() {
      return this.translation.errorMessage[this.currentLanguageIndex];
    }
    ,
  },
  methods: {
    toggleLanguage() {
      // 현재 언어 인덱스를 변경하여 다음 언어로 전환
      this.currentLanguageIndex = (this.currentLanguageIndex + 1) % this.languages.length;
      // 여기에서 언어 변경에 따른 다른 작업을 수행할 수 있습니다.
    },
  }
}
</script>
<style scoped>
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 33%; /* 화면 가로폭의 33%를 차지 */
  margin: 0 auto; /* 가운데 정렬 */
  height: 100vh;
  text-align: center;
}


.logo {
  width: 200px; /* 로고 이미지의 너비 */
  height: auto; /* 높이 자동 조정 */
  margin-bottom: 20px; /* 로고와 헤더 사이 간격 추가 */
  align-self: flex-start; /* 로고를 왼쪽으로 정렬 */
}

.page-title {
  color: #197EC3; /* 페이지 제목 색상 변경 */
  margin: 0;
  font-size: 36px; /* 페이지 제목 폰트 사이즈 변경 */
  margin-bottom: 10px; /* 페이지 제목과 헤더 텍스트 사이 간격 추가 */
}

.header {
  background-color: rgba(25, 126, 195, 0.5);
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%; /* 헤더의 너비를 100%로 설정 */
  margin-bottom: 20px; /* 헤더와 내용 사이 간격 추가 */
}

.header-text {
  color: #fff;
  margin: 0; /* 헤더 텍스트의 margin을 0으로 설정 */
  white-space: nowrap; /* 줄 바꿈을 방지하여 한 줄에 텍스트 표시 */
}

.content {
  text-align: left;
}

.button {
  color: #fff;
  background-color: #197EC3;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
  text-align: center;
  margin-top: 20px; /* 버튼 위에 여백 추가 */
  text-decoration: none;
}

.success-message {
  color: blue; /* 성공 메시지 파란색 */
  margin-top: 20px;
}

.error-message {
  color: red; /* 오류 메시지 붉은색 */
  margin-top: 20px;
}

@media screen and (max-width: 768px) {
  .header-text {
    font-size: 14px; /* 작은 화면에서 텍스트 크기를 줄임 */
  }
}

.language-toggle {
  padding: 5px 10px; /* 토글 버튼의 내부 여백 설정 */
  border: 1px solid #197EC3; /* 토글 버튼 테두리 설정 */
  cursor: pointer;
  border-radius: 20px;
  color: #fff;
  background-color: #197EC3;
  margin-left: auto;

}
</style>
